.main__name{
  width: 100%;
  max-width: 720px;
  height: 226px;
  background-image: url('../../../images/turbina.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  /*margin: 0 auto 187px;*/
  /*–°–≤–æ–π—Å—Ç–≤–∞ –¥–æ–±–∞–≤–ª–µ–Ω—ã —á—Ç–æ–±—ã –ø–ª–µ–µ—Ä —Å–º–æ–≥ –¥–≤–∏–≥–∞—Ç—å—Å—è –≤–≤–µ—Ä—Ö*/
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

@media screen and (max-width: 768px) {
  .main__name{
  width: 600px;
  height: 168px;
}
}


@media screen and (max-width: 610px) {
  .main__name{
  width: 500px;
  height: 250px;
}
}

@media screen and (max-width: 518px) {
  .main__name{
  width: 450px;
  height: 200px;
}
}
@media screen and (max-width: 415px) {
  .main__name{
  width: 320px;
  height: 150px;
}
}
