.cover {
  grid-area: cover;
  width: 240px;
  height: 240px;
  border-radius: 4px;
  margin: 0 auto;
  padding-top: 20px;
  /*–û—Ç —ç—Ç–æ–≥–æ –Ω–∞–¥–æ —É–π—Ç–∏ –≤ react-spring*/
  animation-name: animationForPlayer;
  animation-fill-mode: forwards;
  animation-duration: 1s;
  animation-timing-function: ease-out;
}

@media screen and (min-width: 768px) {
  .cover {
    width: 112px;
    height: 112px;
    padding-top: 20px;
    padding-right: 30px;
    padding-bottom: 30px;
  }
}

@media screen and (min-width: 1024px) {
  .cover {
    width: 164px;
    height: 164px;
    padding-top: 0;
    padding-right: 0;
    padding-left: 25px;
  }
}

@media screen and (min-width: 1280px) {
  .cover {
    width: 176px;
    height: 176px;
  }
}
/*–û—Ç —ç—Ç–æ–≥–æ –Ω–∞–¥–æ —É–π—Ç–∏ –≤ react-spring*/
@keyframes animationForPlayer {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  45% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
