@import url("./__header/main__header.css");
@import url("./__logo/main__logo.css");
@import url("./__logo-link/main__logo-link.css");
@import url("./__name/main__name.css");

.main {
  width: 100%;
  margin: 0 auto;
  /* –î–æ–±–∞–≤–ª–µ–Ω—ã —Å–≤-–≤–∞ –¥–ª—è –æ—Ç—Å—Ç—É–ø–æ–≤ –ø–æ –∫—Ä–∞—è–º*/
  box-sizing: border-box;
  /* –°–≤–æ–π—Å—Ç–≤–∞ –¥–æ–±–∞–≤–ª–µ–Ω—ã —á—Ç–æ–±—ã –ø–ª–µ–µ—Ä —Å–º–æ–≥ –¥–≤–∏–≥–∞—Ç—å—Å—è –≤–≤–µ—Ä—Ö*/
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  position: relative;
}

@media screen and (max-width: 350px) {
  .main {
    max-width: 300px;
  }
}
@media screen and (max-width: 410px) {
  .main {
    max-width: 410px;
  }
}
@media screen and (max-width: 600px) {
  .main {
    max-width: 600px;
  }
}

@media screen and (max-width: 768px) {
  .main {
    max-width: 768px;
  }
}

@media screen and (max-width: 1024px) {
  .main {
    max-width: 1024px;
  }
}
